import request from '@/utils/request'

// 
export function loginAPI(data) {
  return request({
    url: '/api/service/GetMethod?GNID=Login',
    method: 'post',
    data
  })
}

// 
export function orgListAPI(data) {
  return request({
    url: '/api/service/GetMethod?GNID=GetOrg',
    method: 'post',
    data
  })
}

