import Layout from '@/layout'
const deskTop = {
  path: '/',
  component: Layout,
  redirect: '/dashboard',
  hidden: false,
  meta: {
    title: '',
    icon: 'dashboard'
  },
  children: [
    {
      path: 'dashboard',
      component: () => import('@/views/dashboard/index'),
      name: 'Dashboard',
      meta: { title: '', affix: true }
    }
  ]
}
export default deskTop

