/* e */
import request from '@/utils/http'

export function getLoginUser(params) {
  return request({
    url: `http://121.41.114.184:8201/customer/getLoginUser`,
    method: 'get',
    params
  })
}
export function getTokenInfo(data) {
  return request({
    url: `http://121.41.114.184:8201/customer/api/login`,
    method: 'post',
    data
  })
}
